import Card from "@mui/material/Card";
import Button from "@mui/material/Button";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";

import CheckIcon from "@mui/icons-material/Check";
import plan2 from "../../assets/plan-02.svg";
const BasicPlan = () => {
  return (
    <section className="basicPlan">
      <Container maxWidth="xl" sx={{ pt: 8, pb: 8 }}>
        <Grid container component="main">
          <Grid
            item
            xs={12}
            sm={6}
            md={6}
            data-aos="fade-down"
            data-aos-duration="2000"
            data-aos-delay={200}
          >
            <CardMedia
              component="img"
              sx={{ width: "70% !important", margin: "auto" }}
              image={plan2}
              alt="random"
              className="cardMedia"
            />
          </Grid>
          <Grid
            item
            xs={12}
            sm={6}
            md={6}
            display={"flex"}
            flexDirection={"column"}
            justifyContent={"center"}
            alignItems={"center"}
          >
            <Typography
              gutterBottom
              variant="h4"
              align="center"
              marginBottom={3}
              data-aos="fade-down"
              data-aos-duration="2000"
              data-aos-delay={200}
            >
              BASIC
              <span className="line"></span>
            </Typography>
            <Typography
              gutterBottom
              variant="h3"
              margin={0}
              textAlign="center"
              data-aos="fade-down"
              data-aos-duration="2000"
              data-aos-delay={300}
            >
              $150
            </Typography>
            <Typography
              gutterBottom
              variant="p"
              color={"text.secondary"}
              textAlign="center"
              marginBottom={3}
              data-aos="fade-down"
              data-aos-duration="2000"
              data-aos-delay={400}
            >
              per year
            </Typography>
            <Typography
              variant="p"
              textAlign="center"
              data-aos="fade-down"
              data-aos-duration="2000"
              data-aos-delay={450}
            >
              The Basics Plan offers a cost-effective option for those who
              require a limited number of beauty sessions. Whether you need
              makeup, nail, or skin care services, this plan provides five
              sessions at a competitive price. Alternatively, you can choose the
              monthly subscription option, allowing you to access beauty
              services throughout the year at a reduced monthly rate.
              <br />
              Monthly Subscription for a year: $15 per month
            </Typography>
            <CardContent sx={{ textAlign: "center" }}>
              <div
                className="d-flex "
                data-aos="fade-down"
                data-aos-duration="2000"
                data-aos-delay={500}
              >
                <CheckIcon
                  sx={{ color: "secondary.main", fontSize: "1.5em" }}
                />{" "}
                <h4 style={{ "margin-bottom": "0", "margin-left": "10px" }}>
                  5 sessions
                </h4>
              </div>
              <div
                className="d-flex"
                data-aos="fade-down"
                data-aos-duration="2000"
                data-aos-delay={600}
              >
                <CheckIcon
                  sx={{ color: "secondary.main", fontSize: "1.5em" }}
                />{" "}
                <h4 style={{ "margin-bottom": "0", "margin-left": "10px" }}>
                  Makeup, nail, or skin care services
                </h4>
              </div>
              <div
                className="d-flex"
                data-aos="fade-down"
                data-aos-duration="2000"
                data-aos-delay={700}
              >
                <CheckIcon
                  sx={{ color: "secondary.main", fontSize: "1.5em" }}
                />{" "}
                <h4 style={{ "margin-bottom": "0", "margin-left": "10px" }}>
                  Monthly subscription option
                </h4>
              </div>

              <Button
                variant="contained"
                size="large"
                className="secondbtn"
                data-aos="fade-up"
                data-aos-duration="2000"
                data-aos-delay="1300"
                sx={{ marginTop: "20px" }}
                // onClick={handleAbout}
              >
                Choose Plan
              </Button>
            </CardContent>
          </Grid>
        </Grid>
      </Container>
    </section>
  );
};
export default BasicPlan;
