import React, { useState } from "react";
import Grid from "@mui/material/Grid";
import Container from "@mui/material/Container";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import TextareaAutosize from "@mui/base/TextareaAutosize";
import inquiryText from "../assets/inquiry.svg";
import Select, { SelectChangeEvent } from "@mui/material/Select";
import FileUploadOutlinedIcon from "@mui/icons-material/FileUploadOutlined";
import Input from "@mui/material/Input";
import inquiryImg from "../assets/inquiry-01.png";
import Footer from "../components/Footer"
const Inquiry = () => {
  const [type, setType] = useState("");
  const [selectedImage, setSelectedImage] = useState(null);

  const handleImageChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      setSelectedImage(URL.createObjectURL(file));
    }
  };
  const handleChangeInquiry = (event) => {
    setType(event.target.value);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    // Add your form submission logic here
  };
  return (
    <React.Fragment>
      <section className="inquiryHeader">
        <Container maxWidth="xl">
          <Grid
            container
            component="main"
            sx={{ pt: 8 }}
            columns={{ xs: 4, sm: 8, md: 12 }}
          >
            <Grid
              item
              xs={12}
              sm={12}
              md={6}
              elevation={6}
              alignItems="center"
              justifyContent="center"
              display={"flex"}
              boxShadow={"none"}
            >
              <Box
                sx={{
                  my: 8,
                  mx: 4,
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                }}
                data-aos="fade-right"
                data-aos-duration="2000"
                data-aos-delay="0"
              >
                <Container maxWidth="md">
                  <img
                    src={inquiryText}
                    alt=""
                    data-aos="fade-right"
                    data-aos-duration="2000"
                    data-aos-delay={300}
                  />
                  <Typography
                    paragraph
                    data-aos="fade-down"
                    data-aos-duration="2000"
                    data-aos-delay={600}
                  >
                    Make an inquiry and experience our personalized beauty
                    expertise. Our dedicated team of experts is ready to assist
                    you with skincare, haircare, and makeup questions. Get
                    tailored guidance for your unique beauty needs. Submit your
                    inquiry now and embark on a journey to enhance your beauty.
                  </Typography>
                </Container>
              </Box>
            </Grid>
            <Grid
              item
              xs={12}
              sm={12}
              md={6}
              sx={{ display: "flex", alignItems: "center", paddingTop: "30px" }}
            >
              <img
                src={inquiryImg}
                width="90%"
                alt=""
                data-aos="fade-down"
                data-aos-duration="2000"
                data-aos-delay="200"
              />
            </Grid>
          </Grid>
        </Container>
      </section>
      <section className="  ">
        <Container maxWidth="xl">
          <Grid
            container
            component="main"
            justifyContent="center"
            columns={{ xs: 4, sm: 8, md: 12 }}
          >
            <Grid
              item
              xs={12}
              sm={12}
              md={12}
              alignItems="center"
              justifyContent="center"
              display="flex"
              boxShadow="none"
            >
              <Box
                sx={{
                  my: 8,
                  mx: 4,
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                }}

              >
                  <Box
                    component="form"
                    noValidate
                    onSubmit={handleSubmit}
                    sx={{ mt: 3 }}
                  >
                    <Grid container spacing={2}>
                      <Grid item xs={12} sm={6}>
                        <TextField
                          data-aos="fade-down"
                          data-aos-duration="2000"
                          data-aos-delay={300}
                          required
                          fullWidth
                          id="first_name"
                          label="First Name"
                          name="first_name"
                          placeholder="First Name"
                          autoComplete="given-name"
                          autoFocus
                          type="text"
                        />
                      </Grid>
                      <Grid item xs={12} sm={6}>
                        <TextField
                          data-aos="fade-down"
                          data-aos-duration="2000"
                          data-aos-delay={300}
                          required
                          fullWidth
                          id="lastName"
                          label="Last Name"
                          name="last_name"
                          type="text"
                          placeholder="Last Name"
                          autoComplete="family-name"
                        />
                      </Grid>
                      <Grid item xs={12} sm={6}>
                        <TextField
                          data-aos="fade-down"
                          data-aos-duration="2000"
                          data-aos-delay={600}
                          required
                          fullWidth
                          id="email"
                          label="Email Address"
                          name="email"
                          type="email"
                          placeholder="Email"
                          autoComplete="email"
                        />
                      </Grid>
                      <Grid item xs={12} sm={6}>
                        <FormControl
                          fullWidth
                          required
                          data-aos="fade-down"
                          data-aos-duration="2000"
                          data-aos-delay={600}
                        >
                          <InputLabel id="demo-simple-select-helper-label">
                            Inquiry Type
                          </InputLabel>
                          <Select
                            id="inquiryType"
                            value={type}
                            onChange={handleChangeInquiry}
                          >
                            <MenuItem value="">
                              <em>None</em>
                            </MenuItem>
                            <MenuItem value="skincare">Skincare</MenuItem>
                            <MenuItem value="makeup">Makeup</MenuItem>
                            <MenuItem value="nailcare">Nailcare</MenuItem>
                          </Select>
                        </FormControl>
                      </Grid>
                      <Grid item xs={12} sm={6}>
                        <TextareaAutosize
                          data-aos="fade-down"
                          data-aos-duration="2000"
                          data-aos-delay={1800}
                          required
                          color="neutral"
                          minRows={2}
                          placeholder="Please describe your area of concern ...."
                          size="lg"
                          variant="outlined"
                        />
                      </Grid>
                      <Grid item xs={12} sm={6}>
                        <input
                          data-aos="fade-down"
                          data-aos-duration="2000"
                          data-aos-delay={1800}
                          accept="image/*"
                          id="image-upload"
                          type="file"
                          onChange={handleImageChange}
                          style={{ display: "none" }}
                        />
                        <Input
                          data-aos="fade-down"
                          data-aos-duration="2000"
                          data-aos-delay={1800}
                          id="upload-button"
                          type="text"
                          style={{
                            width: "100%",
                            display: "flex",
                            flexDirection: "column",
                          }}
                          readOnly
                          disableUnderline
                          placeholder="Select an image which helps to describe your inquiry"
                          value={selectedImage ? selectedImage.name : ""}
                          endAdornment={
                            <Button
                              variant="contained"
                              className="mainbtn"
                              style={{ width: "100%" }}
                            >
                              Browse
                              <FileUploadOutlinedIcon color="white"></FileUploadOutlinedIcon>
                            </Button>
                          }
                        />
                        {selectedImage && (
                          <div
                            style={{
                              display: "flex",
                              justifyContent: "center",
                            }}
                          >
                            <img
                              src={selectedImage}
                              alt="Uploaded"
                              style={{
                                marginTop: "16px",
                                maxWidth: "100%",
                                maxHeight: "250px",
                              }}
                            />
                            <Typography variant="caption" color="textSecondary">
                              {selectedImage.name}
                            </Typography>
                          </div>
                        )}
                      </Grid>
                    </Grid>
                    <Button
                      data-aos="fade-down"
                      data-aos-duration="2000"
                      data-aos-delay={1200}
                      type="submit"
                      fullWidth
                      variant="contained"
                      sx={{ mt: 3, mb: 2, bgcolor: "secondary.main" }}
                    >
                      Submit my inquiry
                    </Button>
                  </Box>
              </Box>
            </Grid>
          </Grid>
        </Container>
      </section>
      <Footer/>
    </React.Fragment>
  );
};
export default Inquiry;
