import { createSlice } from "@reduxjs/toolkit";
import axios from "axios";

const initialState = {
  status: null,
};

const authSlice = createSlice({
  name: "auth",
  initialState,
  reducers: {
    loginSuccess(state, action) {
      state.status = action.payload;
    },
    logout(state, action) {
      state.status = action.payload;
    },
    userInfo(state, action) {
      state.user = action.payload;
    },
  },
});

export const { loginSuccess, userInfo, logout } = authSlice.actions;

export const login = (customerEmail, customerPassword) => async (dispatch) => {
  try {
    const response = await axios.post(
      "http://localhost:8080/api/customers/login",
      null,
      {
        params: {
          customerEmail,
          customerPassword,
        },
      }
    );
    dispatch(loginSuccess(response.data));
    // Dispatch other actions if needed
  } catch (error) {
    dispatch(loginSuccess(error.response));
    console.log(error.response)
  }
};


export const deleteToken = () => (dispatch) => {
  // Perform any necessary actions related to logout
  dispatch(logout(null));
};

export default authSlice.reducer;
